<template>
  <div class="setting-psw1 view-background">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <section class="psw-content">
      <div class="psw-iput"><label>手机号</label><input type="number" v-model="phone" placeholder="请输入手机号码"></div>
      <div class="psw-iput"><label>验证码</label><input type="text" v-model="vcode" placeholder="请输入6位短信验证码"><span @click="getCode">{{text}}</span></div>
    </section>
    <div class="setting-psw-btn" @click="next" :class="{'ac-sub':!isSubmit}">下一步</div>
  </div>
</template>

<script>
  import {getCode} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        fixed: false,
        phone: '',
        title: '修改密码',
        routerName: '',
        isSend:false,
        text: '获取验证码',
        vcode: '',
        isSubmit: false
      }
    },
    created: function () {
      if (this.$route.params.pageTitle) {
        localStorage.setItem('pageTitle', this.$route.params.pageTitle)
      }
      if (this.$route.params.routerName) {
        localStorage.setItem('routerName', this.$route.params.routerName)
      }
      this.routerName = this.$route.params.routerName === undefined ? localStorage.routerName : this.$route.params.routerName
      this.title = this.$route.params.pageTitle === undefined ? localStorage.pageTitle : this.$route.params.pageTitle
    },
    methods: {
      next: function () {
        var _this = this
        if (!this.isActive) {
          return false
        }
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(_this.phone)) {
          _this.$toast('请输入正确的手机号码')
          return
        }
        if(_this.vcode == '' || _this.vcode == ' '){
          _this.$toast('验证码不能为空')
          return
        }
        _this.$router.push({
          name: 'resetpsw',
          params: {
            phone: _this.phone,
            vcode: _this.vcode
          }
        })
      },
      getCode: function () {
        var _this = this
      	if(this.isSend){
      		_this.$toast('验证码发送中，请稍后')
      		return
      	}
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(_this.phone)) {
          _this.$toast('请输入正确的手机号码')
          return false
        }
        this.tempTime = 120
        this.text = '120秒后重发'
        this.sendBtnTimer = setInterval(this.time, 1000)
        getCode(this.phone).then(function (rep) {
           _this.$toast('获取验证码成功，请查看手机')
      		_this.isSend = true
        }).catch(function () {
            _this.$toast('获取验证码失败！')
        })
      },
      time: function () {
        this.tempTime--
        if (this.tempTime < 1) {
          this.text = '发送验证码'
          clearInterval(this.sendBtnTimer)
      		this.isSend = false
          return false
        }
        this.text = this.tempTime + '秒后重发'
      }
    },
    computed: {
      isActive: function () {
        if (this.phone === ''|| this.vcode === '') {
          return false
        } else {
          return true
        }
      }
    },
    watch: {
      isActive: function(newValue, oldValue) {
        this.isSubmit = newValue
      }
    }
  }
</script>

<style>
.ac-sub{
  background-color: rgba(200,200,200,.4);
}
</style>


