import axios from 'axios'
import qs from 'qs'
let baseUrl = '/static/data/'
let serverUrl = '/api'
export const getProtocol = axios(baseUrl + 'protocol.json')
export const getrefundDetails = axios(baseUrl + 'refundDetails.json')

// 首页 行业分类
export const getFenLei = () => axios(serverUrl + '/storeCategory/list')
// 首页 轮播广告
export const getSwiper = () => axios(serverUrl + '/media/list/big')
// 首页 banner广告
export const getGuanggao = () => axios(serverUrl + '/media/list/small')
//通知列表
export const getNoticeList = () => axios(serverUrl + '/media/notice/list')

//通知详情
export const getNoticeContent = (id) =>axios(serverUrl + '/media/notice/getById/' + id)


// 用户登录
export const postLogin = (username,password) => axios.post(serverUrl + '/member/login/', qs.stringify({
  username: username,
  password: password
}))

// 获取验证码
export const getCode = mobile => axios(serverUrl + '/member/getCode/' + mobile)

// 用户注册
export const postRegister = (mobile, inviteCode,password,vcode) => axios.post(serverUrl + '/member/register/', qs.stringify({
  mobile: mobile,
  inviteCode: inviteCode,
  password: password,
  vcode: vcode
}))

// 重置/找回密码
export const postReset = (mobile,password,vcode) => axios.post(serverUrl + '/member/reset/', qs.stringify({
  mobile: mobile,
  password: password,
  vcode: vcode
}))

//修改个人信息
export const postMemberUpdate = (nickName) => axios.post(serverUrl + '/member/update/', qs.stringify({
  nickName: nickName
}))

// 验证码登录
export const postVCodeLogin = (mobile,vcode) => axios.post(serverUrl + '/member/verifyCode/login/', qs.stringify({
  mobile: mobile,
  vcode: vcode
}))


// 退出登录
export const getLogout = () => axios(serverUrl + '/member/logout')
// 我的 （个人信息）
export const getMe = () => axios(serverUrl + '/member/me')

// 我的推荐
export const getMyRecommend = () => axios(serverUrl + '/member/myRecommend')

/* 城市列表  */
export const getAllCity = (name) => {
  if (name == undefined) {
    return axios(serverUrl + '/area/getCitySortByChar')
  } else {
    return axios(serverUrl + '/area/getCitySortByChar/?name=' + name)
  }
}
/* 购物车列表(需登录)*/
export const getCartList = () => axios(serverUrl + '/cart/getCartList')
/* 获取首页分类下的所有商品  */
export const getByStoreCategory = (pageNum,pageSize,storeCategoryId,latitude,longitude) => axios.post(serverUrl + '/store/getByStoreCategoryId?categoryId=' ,qs.stringify({
  storeCategoryId:storeCategoryId,
  cityCode:440300,
  latitude:latitude,
  longitude:longitude,
  pageNum:pageNum,
  pageSize:pageSize
}))
/* 附件商家列表 */
export const getNearStore = (pageNum,pageSize,latitude,longitude) => axios.post(serverUrl + '/store/nearStore/', qs.stringify({
  latitude: latitude,
  longitude: longitude,
  pageNum: pageNum,
  pageSize: pageSize
}))
/* 商家首页  */
export const getStore = (storeId, latitude, longitude) => axios.post(serverUrl + '/store/getStore', qs.stringify({
  storeId: storeId,
  latitude: latitude,
  longitude: longitude
}))
/* 商家首页下的分类商品   */
export const getProductCategory = categoryId => axios(serverUrl + '/product/getByProductCategoryId?categoryId=' + categoryId)
/* 加入购物车  */
export const addCart = (skuId,amount) => axios.post(serverUrl + '/cart/add' , qs.stringify({
  skuId: skuId,
  amount:amount||1,
  
}))
/* 商家首页商品详情   */
export const getProductBySkuId = (skuId, latitude, longitude) => axios.post(serverUrl + '/product/getBySkuId', qs.stringify({
  skuId: skuId,
  latitude: latitude,
  longitude: longitude
}))
/* 获取商品的属性  */
export const getAttrBySpuId = SpuId => axios(serverUrl + '/product/getAttrBySpuId/' + SpuId)
/* 根据选中的属性来匹配商品 */
export const getAttrOption = SpuId => axios(serverUrl + '/product/getAttrOptionBySpuId/' + SpuId)
/* 删除购物车的商品  */
export const deletCart = (skuIds) => axios.post(serverUrl + '/cart/delete' ,qs.stringify({
  skuId: skuIds
}))
/*  修改单个商品的数量  */
export const updateAmount = (skuId,amount) => axios.post(serverUrl + '/cart/updateAmount',qs.stringify({
  skuId: skuId,amount,
  amount: amount
}))

/* 我的收益 chart */
export const getProfitChart = days => axios.post(serverUrl + '/member/profit/pictureData?days=' + days)

/* 我的收益详情 */
export const getProfitList = days => axios(serverUrl + '/member/profit/getDetailData?days=' + days)

/* 提交订单 */
export const confirmOrder = (obj) => axios.post(serverUrl + '/order/addOrder', obj)
/* 订单列表  */
export const getOrderList = (orderStatus,pageNum,pageSize,startTime,endTime) => axios.post(serverUrl + '/order/getOrderList',qs.stringify({
  orderStatus: orderStatus,
  pageNum: pageNum ||1,
  pageSize: pageSize || 15,
  startTime: startTime,
  endTime: endTime,
}))
/* 收藏商家  */
export const addCollectStore = storeId => axios(serverUrl + '/collect/store/' + storeId)
/* 收藏商品  */
export const addCollectgood = skuId => axios(serverUrl + '/collect/product/' + skuId)
// 我收藏的产品
export const getMyCollection = () => axios(serverUrl + '/collect/getMyCollectProduct')
/* 我的收藏的商家  */
export const getMyCollectStore = () => axios(serverUrl + '/collect/getMyCollectStore')
/* 取消收藏 */
export const cancelMyCollect = (collectId) => axios.post(serverUrl + '/collect/cancel',qs.stringify({
  collectId: collectId
}))
/* 搜索商品 */
export const getProductLikeName = (name) => axios.post(serverUrl + '/product/getProductLikeName' , qs.stringify({
  name: name
}))
/* 搜索商家  */
export const getStoreLikeName = (name) => axios.post(serverUrl + '/store/getStoreLikeName' , qs.stringify({
  name: name
}))
/* 查看订单详情*/
export const getOrder = orderNo => axios(serverUrl + '/order/getOrder/' + orderNo)



